import React from "react";
import { BrowserRouter, HashRouter, Route } from "react-router-dom";

// CSS Styles
import "../css/Main.css";

// Components
import Menu from "./Menu";
import CompactMenu from "./CompactMenu";

import HomePage from "./Pages/homePage";
import ContentPage from "./Pages/contentPage";
import AuthorsPage from "./Pages/authorsPage";
import AboutPage from "./Pages/aboutPage";

const Simple = (els) => <div>{els.hello}</div>;

const Main = (props) => {
  let menu = props.menu;
  return (
    <BrowserRouter>
      <React.StrictMode>
        <main>
          <CompactMenu {...props} />
          <Menu {...props} />
          <Route
            exact
            path={`/${props.menu.home}`}
            render={(props) => <HomePage {...props} menu={menu} />}
          />
          <Route
            exact
            path={`/${props.menu.content}`}
            component={ContentPage}
          />
          <Route
            exact
            path={`/${props.menu.authors}*`}
            component={AuthorsPage}
          />
          <Route exact path={`/${props.menu.about}`} component={AboutPage} />
        </main>
      </React.StrictMode>
    </BrowserRouter>
  );
};

export default Main;
